import { Button, Paper } from "@mui/material";
import { makeStyles } from "@mui/styles";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useLocation } from "react-router";
import { setLoadingMethods } from "../../../redux/reducers/replenishment";

const Navigation = ({ t, mode }) => {
  const history = useHistory();
  const classes = useStyles();
  const location = useLocation();
  const dispatch = useDispatch();

  const { replenishmentCategories } = useSelector(
    ({ replenishmentReducer }) => replenishmentReducer
  );

  return (
    <Paper
      className={classes.box}
      sx={{
        background: mode === "light" && "#FFF",
        boxShadow: mode === "light" && "0px 1px 0px #E2E8F0",
      }}
    >
      {Object.values(
        typeof replenishmentCategories?.data?.categories === "object" &&
          replenishmentCategories?.data?.categories
      )?.map((item, idx) => (
        <Button
          key={idx}
          variant="contained"
          className={
            location.pathname === `/dashboard/replenish/${item.slug}`
              ? classes.button_active
              : classes.button
          }
          onClick={() => {
            history.push(`/dashboard/replenish/${item.slug}`);
            dispatch(setLoadingMethods(true));
          }}
          size="large"
          color="primary"
        >
          {item.name}
        </Button>
      ))}
    </Paper>
  );
};

export default Navigation;

const useStyles = makeStyles((theme) => ({
  button: {
    marginBottom: 16,
    width: "100%",
    background: "transparent",
    color: "#B8C0CC",
    boxShadow: "none!important",

    "&:hover": {
      "& .MuiButton-label": {
        color: "#000",
      },
    },
  },

  button_active: {
    marginBottom: 16,
    width: "100%",
    background: "#FCE34D",
    color: "#000",
    boxShadow: "none!important",
  },

  box: {
    // background: "#fff",
    // boxShadow: " 0px 1px 0px #E2E8F0",
    borderRadius: 13,
    display: "flex",
    flexDirection: "column",
    padding: 24,
  },
}));
