/* eslint-disable eqeqeq */
import { Box } from "@mui/system";
import React from "react";

import { Typography } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import {
  getReplenishmentData,
  setCryptoDialog,
} from "../../../../redux/reducers/replenishment";
import { makeStyles } from "@mui/styles";

const Card = ({ item, t, setVerificationPush, mode }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const account = profileData?.data.profile.verification.value;
  return (
    <Box
      sx={{
        backgroundColor: mode === "light" && "#FFFFFF!important",
        boxShadow: mode === "light" && "0px 1px 0px #E2E8F0",
      }}
      className={classes.box}
      onClick={() => {
        if (account == 2) {
          dispatch(
            setCryptoDialog({
              open: true,
              name: item.name,
              currency: item.currency,
            })
          );
          dispatch(getReplenishmentData(item.category, { method: item.slug }));
        } else setVerificationPush(true);
      }}
    >
      <img src={item.logo} alt="" />
      <Box className={classes.textBox}>
        <Typography className={classes.title}>{item.currency}</Typography>
        <Typography className={classes.subtitle}>{item.name}</Typography>
      </Box>
    </Box>
  );
};

export default Card;

const useStyles = makeStyles((theme) => ({
  box: {
    cursor: "pointer",
    borderRadius: 8,
    height: 167,
    display: "flex",
    justifyContent: "flex-start",
    alignItems: "center",
    padding: "30px",
    transition: "all 0.2s linear",
    "&:hover": {
      boxShadow: "0px 0px 6px 2px #E2E8F0",
    },
    [theme.breakpoints.down(576)]: {
      display: "flex",
      flexDirection: "column",
    },
    "& img": {
      objectFit: "contain",
      width: 64,
      height: 64,
      marginRight: 16,

      [theme.breakpoints.down(992)]: {
        marginRight: 10,
      },

      [theme.breakpoints.down(576)]: {
        marginBottom: 15,
      },
    },
  },

  textBox: {
    maxWidth: "77%",
    overflow: "hidden",
  },

  title: {
    fontSize: 33,
    fontWeight: "500",
    lineHeight: "40px",
    marginBottom: 4,
    textOverflow: "ellipsis",
    overflow: "hidden",
    whiteSpace: "nowrap",
    maxWidth: "100%",

    [theme.breakpoints.down(992)]: {
      fontSize: 30,
    },
  },

  subtitle: {
    color: "#64748B",
    fontSize: 16,
    lineHeight: "19px",
    textOverflow: "ellipsis",
    overflow: "hidden",
    maxWidth: "100%",

    [theme.breakpoints.down(576)]: {
      textAlign: "center",
    },
  },
}));
