/* eslint-disable eqeqeq */
import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  setFiatDialog,
  setInfoDialog,
} from "../../../redux/reducers/replenishment";
import { WhiteBox } from "../../../shared/Boxes";
import { makeStyles } from "@mui/styles";

const Card = ({ item, terminals, cash, mode, setVerificationPush }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const account = profileData?.data.profile.verification.value;

  return (
    <WhiteBox
      className={classes.box}
      sx={{
        background: mode === "light" && "#fff",
        boxShadow: mode === "light" && "0px 1px 0px #E2E8F0",
        color: mode === "light" ? "#000" : "#FFF",
      }}
      onClick={() => {
        if (account == 2) {
          if (!terminals && !cash) {
            dispatch(
              setFiatDialog({
                open: true,
                name: item.currency,
                cash: item.name,
                type: item.name,
                currency: item.currency,
                commission: item.commission,
                category: item.category,
                slug: item.slug,
              })
            );
          } else  {
            return dispatch(setInfoDialog({ open: true, name: item.name }));
          }
        } else setVerificationPush(true);
      }}
    >
      <img src={item.logo} alt="" />
      <Box className={classes.textBox}>
        <Typography className={classes.title}>{item.currency}</Typography>
        <Typography className={classes.subtitle}>{item.name}</Typography>
      </Box>
      <Box className={classes.saleBlock}>
        <Typography variant="body1" sx={{ color: mode === "light" && "#FFF" }}>
          {item.commission}%
        </Typography>
      </Box>
    </WhiteBox>
  );
};

export default Card;

const useStyles = makeStyles((theme) => ({
  box: {
    // background: "#fff",

    padding: "17px 0 0 0",
    height: 194,
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    flexDirection: "column",
    cursor: "pointer",
    "& img": {
      width: 64,
      height: 64,
      objectFit: "contain",
    },
    borderRadius: 8,
    transition: "all 0.2s linear",
    "&:hover": {
      boxShadow: "0px 0px 6px 2px #E2E8F0",
    },
    [theme.breakpoints.down(576)]: {
      display: "flex",
      flexDirection: "column",
    },
  },
  title: {
    fontSize: 33,
    fontWeight: "500",
    lineHeight: "40px",
    // color: "#000000",
    marginBottom: 4,
    color: "inherit",
    textOverflow: "ellipsis",
    overflow: "hidden",
    whiteSpace: "nowrap",
    maxWidth: "100%",
    textAlign: "center",

    [theme.breakpoints.down(992)]: {
      fontSize: 30,
    },
  },

  textBox: {
    overflow: "hidden",
    maxWidth: "90%",
  },

  subtitle: {
    color: "#64748B",
    fontSize: 16,
    lineHeight: "19px",
    textOverflow: "ellipsis",
    overflow: "hidden",
    maxWidth: "100%",
    textAlign: "center",
    whiteSpace: "nowrap",
  },

  saleBlock: {
    width: "100%",
    maxHeight: 35,
    background: "#0D0F11",
    textAlign: "center",
    borderRadius: "0 0 8px 8px",
    marginTop: 8,
    padding: "6px 0",
  },
}));
