import React, { useEffect } from "react";
import { Grid, useMediaQuery, useTheme } from "@mui/material";
import Card from "./Card";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import { getReplenishmentMethods } from "../../../../redux/reducers/replenishment";
import CryptoSkeleton from "../../../../components/CryptoSkeleton";
import DialogCrypto, { DrawerReplenish } from "./Dialog";

const Crypto = ({ t, setVerificationPush, mode }) => {
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));
  const dispatch = useDispatch();

  const { replenishmentMethods, isLoadingMethods } = useSelector(
    ({ replenishmentReducer }) => replenishmentReducer
  );
  useEffect(() => {
    dispatch(getReplenishmentMethods("cryptocurrency"));
    // eslint-disable-next-line
  }, []);

  return (
    <Grid
      container
      spacing={2}
      style={!tablet ? { width: "75%" } : { width: "100%", marginTop: 24 }}
    >
      {tablet ? <DrawerReplenish t={t} /> : <DialogCrypto t={t} />}
      {isLoadingMethods
        ? Array.from(Array(4).keys()).map((item, idx) => (
            <Grid item xs={12} key={idx} sm={6} md={6} lg={4} xl={4}>
              <CryptoSkeleton key={idx} style={{ width: 389 }} />
            </Grid>
          ))
        : Object.values(
            typeof replenishmentMethods?.data?.methods === "object" &&
              replenishmentMethods?.data?.methods
          )?.map((item, idx) => (
            <Grid item xs={6} key={idx} sm={6} md={6} lg={4} xl={4}>
              <Card
                mode={mode}
                setVerificationPush={setVerificationPush}
                item={item}
                t={t}
              />
            </Grid>
          ))}
    </Grid>
  );
};

export default withRouter(Crypto);
